import React, { Component, useState, useEffect } from 'react'
import { connect } from 'react-redux';
import { BiSearch } from 'react-icons/bi'

function SnapShot(props) {
    const [users, setUsers] = useState([]);
    const [search, setSearch] = useState([]);



    const getUsers = async () => {
        const response = await fetch("https://api.flickr.com/services/rest/?method=flickr.galleries.getPhotos&api_key=ba066da85ca833fb04567af1b5897aba&text=mountain&gallery_id=66911286-72157647277042064&format=json&nojsoncallback=1");
        const data = await response.json()
        console.log(data);
        setUsers(data.photos.photo);
        // const Mytodo=JSON.stringify(users)
        localStorage.setItem("users", JSON.stringify(users))
    }

    useEffect(() => {
        getUsers();

    }, []);

    const searchData = async () => {
        const response = await fetch(`https://www.flickr.com/services/rest/?method=flickr.photos.search&api_key=79fb9f45b1e46580c747cc43611210ea&text=${search}&format=json&nojsoncallback=1`);
        const data = await response.json()
        console.log(data);
        setUsers(data.photos.photo);
    }
    const filData = async () => {
        const response = await fetch("https://www.flickr.com/services/rest/?method=flickr.photos.search&api_key=79fb9f45b1e46580c747cc43611210ea&text=mountain&format=json&nojsoncallback=1");
        const data = await response.json()
        console.log(data);
        setUsers(data.photos.photo);
    }

    const filData1 = async () => {
        const response = await fetch("https://www.flickr.com/services/rest/?method=flickr.photos.search&api_key=79fb9f45b1e46580c747cc43611210ea&text=beaches&format=json&nojsoncallback=1");
        const data = await response.json()
        console.log(data);
        setUsers(data.photos.photo);
    }

    const filData2 = async () => {
        const response = await fetch("https://www.flickr.com/services/rest/?method=flickr.photos.search&api_key=79fb9f45b1e46580c747cc43611210ea&text=birds&format=json&nojsoncallback=1");
        const data = await response.json()
        console.log(data);
        setUsers(data.photos.photo);
    }

    const filData3 = async () => {
        const response = await fetch("https://www.flickr.com/services/rest/?method=flickr.photos.search&api_key=79fb9f45b1e46580c747cc43611210ea&text=food&format=json&nojsoncallback=1");
        const data = await response.json()
        console.log(data);
        setUsers(data.photos.photo);
    }




    return (
        <div>
            <h1 className="mt-4 text-center main-heading" style={{ color: "black", fontFamily: "Times New Roman" }}> Welcome {props.userInfo.userName} to Snapshot</h1>

            <input type="text" value={search} onChange={(event) => setSearch(event.target.value)} />
            <button style={{ padding: "15px 40px", border: "none", backgroundColor: "black", color: "white" }} onClick={(search) => searchData()} ><BiSearch /></button>


            <div >
                {/* <div className="buttons d-flex jusify-content-around"> */}
                <button class="button" onClick={() => filData()} >Mountain</button>
                <button class="button" onClick={() => filData1()} >Beaches</button>
                <button class="button" onClick={() => filData2()} >Birds</button>
                <button class="button" onClick={() => filData3()} >Food</button>
                {/* </div> */}
            </div>
            <h1>{search}</h1>

            {
                users.map((currElement) => {
                    return (
                        <div className="display" >
                            <div>
                                {/* <p>{currElement.title}</p> */}
                                {/* <div key={currElement.id} className="imgClass"> */}
                                <img src={`https://farm${currElement.farm}.staticflickr.com/${currElement.server}/${currElement.id}_${currElement.secret}_m.jpg`} style={{ borderRadius: "10px" }} />

                            </div>
                        </div>
                    )
                })
            }


        </div>
    )

}
const mapStateToProps = (state) => {
    return {
        userInfo: state.userDetails
    }

}

export default connect(mapStateToProps, null)(SnapShot);

