import React from 'react'
import './App.css';
import {Switch} from 'react-router-dom'
import {BrowserRouter, Route} from 'react-router-dom'
import HeaderBar from './Component/Header/HeaderBar'
import Home from './Component/Otherpages/Home'
import Login from './Component/Otherpages/Login'
import Signup from './Component/Otherpages/SignUp'
import Logout from './Component/Otherpages/Logout'
import SnapShot from './Component/Otherpages/SnapShot'

function App() {
  return (
    <div className="App">
           <BrowserRouter>
    <HeaderBar/>   
      <Switch>
      <Route exact path="/" component={Home}/>
      <Route exact path="/login" component={Login}/>
      <Route exact path="/logout" component={Logout}/>
      <Route exact path="/signup" component={Signup}/>
      <Route exact path="/snapshot" component={SnapShot}/>
      </Switch>
     </BrowserRouter>
     
    </div>
  );
}

export default App;
